import { ethers } from "ethers";
import { BigNumber } from "@ethersproject/bignumber";
import { Info } from "../types";
import { abi } from "./configs";

export const initialize = () => {
  const { ethereum } = window as any;
  return Boolean(ethereum && ethereum.isMetaMask);
};

export const loadEnv = () => {
  const contractAddress = process.env.REACT_APP_CONTRACT as string;
  const supportedChainId = Number(process.env.REACT_APP_CHAIN_ID);
  return { contractAddress, supportedChainId };
};

export const connectMetamask = async () => {
  try {
    // Will open the MetaMask UI
    // You should disable this button while the request is pending!
    const { ethereum } = window as any;
    const res = await ethereum.request({ method: "eth_requestAccounts" });
    return res[0] as string;
  } catch (error) {
    console.error(error);
    return "";
  }
};

export const installMetaMask = () => {
  window.alert("Please install MetaMask extension first");
};

export const getInfo = async (): Promise<Info> => {
  const { contractAddress } = loadEnv();
  const provider = new ethers.providers.Web3Provider((window as any).ethereum);
  const contract = new ethers.Contract(contractAddress, abi, provider);
  const info = await contract.info();
  const minted = (info[0] as BigNumber).toNumber();
  const total = (info[1] as BigNumber).toNumber();
  const price = Number(ethers.utils.formatUnits(info[2], 18));
  return { minted, total, price };
};

export const mint = async () => {
  const iface = new ethers.utils.Interface(abi);
  const ethereum = (window as any).ethereum;
  const { contractAddress } = loadEnv();

  const data = iface.encodeFunctionData("purchase", []);
  const transactionParameters = {
    to: contractAddress, // Required except during contract publications.
    from: ethereum.selectedAddress, // must match user's active address.
    data,
  };

  // txHash is a hex string
  // As with any RPC call, it may throw an error
  const txHash = await ethereum.request({
    method: "eth_sendTransaction",
    params: [transactionParameters],
  });
  console.log(txHash);
};

export const checkConnection = async () => {
  const ethereum = (window as any).ethereum;
  try {
    const accounts = await ethereum.request({ method: "eth_accounts" });
    return accounts[0];
  } catch (err) {
    return "";
  }
};
