import { useEffect, useState } from "react";
import { Info } from "../types";
import Spinner from "./Spinner/Spinner";

import run from "../../src/icon/run.gif";
import zzz from "../../src/icon/zzz.png";
import L from "../../src/icon/L.png";
import Five from "../../src/icon/Five.png";
import Logout from "../../src/icon/logout.png";
import Play from "../../src/icon/play.png";
import OpenSea from "../../src/icon/open_sea.svg";
import Discord from "../../src/icon/discord.png";

import Land from "../../src/icon/green_land.png";
import MegaRock from "../../src/icon/mega_rock.png";
import MegaRockMobile from "../../src/icon/logo_mobile.svg";

interface IProps {
  address?: string;
  onGetStarted: () => void;
  onLoadInfo: () => Promise<Info>;
  onMint: () => void;
  onDisconnect?: () => void;
}
const Welcome = ({
  address,
  onMint,
  onGetStarted,
  onLoadInfo,
  onDisconnect,
}: IProps) => {
  const [info, setInfo] = useState<Info | undefined>();
  useEffect(() => {
    onLoadInfo().then(setInfo).catch(window.alert);
  }, [onLoadInfo]);

  return info ? (
    <>
      <div className="container-app">
        {address ? (
          <div className="header-button">
            {onDisconnect && (
              <button
                className="btn btn-custom"
                title="Logout"
                onClick={onDisconnect}
              >
                <img width={30} height={30} src={Logout} alt="Logout" />
              </button>
            )}
          </div>
        ) : (
          <div className="header-button"></div>
        )}
        <div className="group-header">
          <img
            height={120}
            width={600}
            src={MegaRock}
            alt="MegaRock"
            className="image_full"
          />
          <img
            width={450}
            height={100}
            src={MegaRockMobile}
            alt="running"
            className="image_mobile"
          />
        </div>

        <div className="info-container">
          <div className="group-container-info">
            <img className="gif-icon" src={run} alt="running" />
            <div className="detail-info">
              <div className="group-container">
                <p className="info-text">{info.minted}&nbsp;/&nbsp;</p>
                <p className="info-text"> {info.total}</p>
              </div>
              <p className="price-info">{info.price} ETH</p>
            </div>
          </div>
          {address ? (
            <button type="button" className="btn btn-grad" onClick={onMint}>
              MINT
            </button>
          ) : (
            <button
              type="button"
              className="btn btn-connect"
              onClick={onGetStarted}
            >
              <img width={15} height={15} src={Play} alt="running" /> <span>CONNECT</span>
            </button>
          )}
        </div>
        <div className="group-first-content">
          <h5 className="heading-content">PROJECT</h5>
          <p className="english-content content-space">
            A thousand residents living in a good old 8-bit village.<br></br>
            Whether they are robots or humans, no one knows nowadays.<br></br>
            There is no war or conflict here.<br></br>
            We Will live in peace for a long time to come.<br></br>
          </p>
          <p className="japan-content content-space">
            bit
            <br></br>
            
          </p>
          <p className="japan-content-mobile content-space">
            bit<br></br>
            <br></br>
            <br></br>
            <br></br>
            
          </p>
        </div>
        <img className="land-divide" height={40} src={Land} alt="Land" />
        <div className="group-content">
          <h5 className="heading-content">TEAM</h5>
          <div className="background-image"></div>
          <div className="group-image">
            <div>
              <div className="background-image-team">
                <img src={zzz} alt="zzz" className="team-image" />
              </div>
              <p className="team-role">(Hacker)</p>
              <p className="team-name">zzz</p>
            </div>
            <div>
              <div className="background-image-team">
                <img src={L} alt="L" className="team-image" />
              </div>
              <p className="team-role">(Hustler)</p>
              <p className="team-name">L</p>
            </div>
            <div>
              <div className="background-image-team">
                <img src={Five} alt="L" className="team-image" />
              </div>
              <p className="team-role">(Hipster)</p>
              <p className="team-name">Five</p>
            </div>
          </div>
        </div>
        <div className="group-footer">
          <div className="group-footer-icon">
            <a href={process.env.REACT_APP_DISCORD}>
              <img width={18} height={18} src={Discord} alt="Discord" />
            </a>
            <a href={process.env.REACT_APP_OPENSEA}>
              <img width={18} height={18} src={OpenSea} alt="OpenSea" />
            </a>
          </div>
          <p className="footer-content">Copyright  MegaRockVille Project</p>
        </div>
      </div>
    </>
  ) : (
    <Spinner />
  );
};

export default Welcome;
