import React, { useCallback, useEffect, useState } from "react";
import {
  connectMetamask,
  initialize,
  installMetaMask,
  getInfo,
  loadEnv,
  mint,
} from "./controllers/ui-helpers";
import "./App.css";
import Welcome from "./components/Welcome";
import { useMetaMaskAccount } from "./hooks/useMetamaskAccount";
import { useMetamaskNetwork } from "./hooks/useMetamaskNetwork";
import NetworkUnSupport from "./components/NetworkUnSupport";

function App() {
  const [metaMaskInstalled, setMetaMaskInstalled] = useState(false);
  const { supportedChainId } = loadEnv();
  const chainInfo = useMetamaskNetwork(supportedChainId);
  const { userAddress, setAddress } = useMetaMaskAccount();

  const onGetStarted = useCallback(async () => {
    if (metaMaskInstalled) {
      const address = await connectMetamask();
      setAddress(address);
    } else {
      installMetaMask();
    }
  }, [metaMaskInstalled, setAddress]);

  const loadInfo = useCallback(() => {
    return getInfo();
  }, []);

  const onMint = useCallback(() => {
    if (userAddress) {
      mint();
    }
  }, [userAddress]);

  const onRequestChangeNetwork = useCallback(() => {
    (window as any).ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: `0x${Number(supportedChainId).toString(16)}` }], // chainId must be in hexadecimal numbers
    });
  }, [supportedChainId]);

  useEffect(() => {
    const isMetaMaskInstalled = initialize();
    setMetaMaskInstalled(isMetaMaskInstalled);
  }, []);

  return (
    <div className="App">
      {!chainInfo ? null : chainInfo.valid ? (
        <Welcome
          address={userAddress}
          onMint={onMint}
          onGetStarted={onGetStarted}
          onLoadInfo={loadInfo}
        />
      ) : (
        <NetworkUnSupport
          chainInfo={chainInfo}
          supportedChainId={supportedChainId}
          onRequestChangeNetwork={onRequestChangeNetwork}
        />
      )}
    </div>
  );
}

export default App;
